@use 'variables' as v;


@mixin phone {
    @media (min-width: v.$phone){
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet){
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop){
        @content;
    }
}

@mixin desktopxl {
    @media (min-width: v.$desktopxl){
        @content;
    }
}

@mixin grid($column, $gap) {
    display: grid;
    grid-template-columns: repeat($column, 1fr);
    gap: $gap;
}

@mixin btn($color, $text) {
    background-color: $color;
    font-size: 2rem;
    padding: 1.2rem 3rem;
    border-radius: 1rem;
    font-weight: 700;
    width: 100%;
    text-align: center;
    color: $text;
    text-decoration: none;
    transition:.3s ease;
    border: none;

    &:hover {
        cursor: pointer;
        opacity: .5;
        color: gray;
    }

/*      @include tablet {
        width: auto;
        display: inline-block;
    }  */
}

@mixin bg-url($url, $url2x: false, $webp1x: false, $webp2x: false, $avif1x: false, $avif2x: false) {
    background-image: url($url);
    @if $webp1x { .webp & { background-image: url($webp1x) }}
    @if $avif1x { .avif & { background-image: url($avif1x) }}
    @if $url2x {
     @media
     screen and (-webkit-min-device-pixel-ratio: 2),
     screen and (min-resolution: 192dpi),
     screen and (min-resolution: 2dppx) {
      background-image: url($url2x);
      @if $webp2x {.webp & {background-image: url($webp2x)}}
      @if $avif2x {.avif & {background-image: url($avif2x)}}
        }
    }
}

@mixin bg-url-degraded($url, $url2x: false, $webp1x: false, $webp2x: false, $avif1x: false, $avif2x: false) {
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url($url);
    @if $webp1x { .webp & { background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url($webp1x) }}
    @if $avif1x { .avif & { background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url($avif1x) }}
    @if $url2x {
     @media
     screen and (-webkit-min-device-pixel-ratio: 2),
     screen and (min-resolution: 192dpi),
     screen and (min-resolution: 2dppx) {
      background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url($url2x);
      @if $webp2x {.webp & {background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url($webp2x)}}
      @if $avif2x {.avif & {background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url($avif2x)}}
        }
    }
}

