@use 'base/mixins' as m;

.header{
    padding: 5rem;

    &__grid{
        display: flex;
        flex-direction: column;
        align-items: center;
        
        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
            
        }
    }

    &__logo{
        width: 15rem;
        margin-bottom: 2rem;
        
        @include m.tablet {
            margin-bottom: unset;
        }
    }

}
