@use 'base/mixins' as m;
@use 'base/variables' as v;

.school{
     @include m.bg-url-degraded('../img/chef-bg.jpg','../img/chef-bg.jpg','../img/chef-bg.webp','../img/chef-bg.webp','../img/chef-bg.avif','../img/chef-bg.avif');
     padding: 20rem 0;
     text-align: center;
     color: v.$white;

     &__title{
        font-weight: 700;
     }
     &__text{
        font-size: 2.6rem;
        max-width: 50rem;
        margin: 0 auto;
     }
     &__link{
      margin-top: 1rem;
      @include m.btn(v.$primary, v.$white ) ;

     }
}