@use 'base/mixins' as m;
@use 'base/variables' as v;

.blocks{

    &__grid{
        @include m.tablet {
            @include m.grid(2, 0rem) ;
            grid-auto-flow: dense;
        }
    }
}

.block{
    height: 40rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    &:nth-child(1){
        background-color: v.$primary;
    }

    &:nth-child(2){
        @include m.bg-url('../img/block1.jpg','../img/block1.jpg','../img/block1.webp','../img/block1.webp','../img/block1.avif','../img/block1.avif');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
    }
    &:nth-child(3){
        background-color: v.$secondary;
        @include m.tablet {
            grid-column: 2/3;
        }
    }
    &:nth-child(4){
        @include m.bg-url('../img/block2.jpg','../img/block2.jpg','../img/block2.webp','../img/block2.webp','../img/block2.avif','../img/block2.avif');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
    }

    &__content{
        max-width: 40rem;
        border-block: 1px solid v.$white ;
        color: v.$white;
        padding: 1rem 0;
    }
    &__title{
        text-transform: uppercase;
        color: v.$white;
    }
    &__text{
        font-size: 2rem;
        color: v.$gray;
    }
}