@use 'base/mixins' as m;
@use 'base/variables' as v;

.navbar{
    display: flex;
    flex-direction: column;
    align-items: center;

    @include m.tablet {
        flex-direction: row;
        justify-content: space-evenly;
        gap: 3rem;
    }
    &__link{
        font-size: 2.4rem;
        margin-bottom: 2rem;
        transition: 0.3s;
        &:hover{
            color:v.$primary;
        }
        &:last-of-type{
            margin-bottom: 0;
        }
        @include m.tablet {
            margin-bottom: unset;
        }
    }
}   