html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  /*     &.body-scroll{
          padding-top: 8.6rem;
      } */
}
body.fix-body {
  overflow: hidden;
}

p {
  font-size: 1.6rem;
  line-height: 2;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
  width: min(90%, 120rem);
  margin: 0 auto;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.section {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .section {
    padding: 8rem 0;
  }
}

.header {
  padding: 5rem;
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 15rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .header__logo {
    margin-bottom: unset;
  }
}

.navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navbar {
    flex-direction: row;
    justify-content: space-evenly;
    gap: 3rem;
  }
}
.navbar__link {
  font-size: 2.4rem;
  margin-bottom: 2rem;
  transition: 0.3s;
}
.navbar__link:hover {
  color: #d92a2a;
}
.navbar__link:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .navbar__link {
    margin-bottom: unset;
  }
}

@media (min-width: 768px) {
  .blocks__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0rem;
    grid-auto-flow: dense;
  }
}

.block {
  height: 40rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.block:nth-child(1) {
  background-color: #d92a2a;
}
.block:nth-child(2) {
  background-image: url("../img/block1.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.webp .block:nth-child(2) {
  background-image: url("../img/block1.webp");
}
.avif .block:nth-child(2) {
  background-image: url("../img/block1.avif");
}
@media screen and (-webkit-min-device-pixel-ratio: 2), screen and (min-resolution: 192dpi), screen and (min-resolution: 2dppx) {
  .block:nth-child(2) {
    background-image: url("../img/block1.jpg");
  }
  .webp .block:nth-child(2) {
    background-image: url("../img/block1.webp");
  }
  .avif .block:nth-child(2) {
    background-image: url("../img/block1.avif");
  }
}
.block:nth-child(3) {
  background-color: #999f44;
}
@media (min-width: 768px) {
  .block:nth-child(3) {
    grid-column: 2/3;
  }
}
.block:nth-child(4) {
  background-image: url("../img/block2.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.webp .block:nth-child(4) {
  background-image: url("../img/block2.webp");
}
.avif .block:nth-child(4) {
  background-image: url("../img/block2.avif");
}
@media screen and (-webkit-min-device-pixel-ratio: 2), screen and (min-resolution: 192dpi), screen and (min-resolution: 2dppx) {
  .block:nth-child(4) {
    background-image: url("../img/block2.jpg");
  }
  .webp .block:nth-child(4) {
    background-image: url("../img/block2.webp");
  }
  .avif .block:nth-child(4) {
    background-image: url("../img/block2.avif");
  }
}
.block__content {
  max-width: 40rem;
  border-block: 1px solid #FFFFFF;
  color: #FFFFFF;
  padding: 1rem 0;
}
.block__title {
  text-transform: uppercase;
  color: #FFFFFF;
}
.block__text {
  font-size: 2rem;
  color: #f5f5f5;
}

.us {
  padding: 5rem 0;
}
.us__title {
  text-align: center;
}
.us__title::after {
  content: "";
  display: block;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/separador.png);
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: 768px) {
  .us__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.us__block {
  text-align: center;
}
.us__img {
  margin: 0 auto;
}

.courses {
  background-color: #f5f5f5;
  padding: 5rem 0;
}
.courses__title {
  text-align: center;
}
.courses__title::after {
  content: "";
  display: block;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/separador.png);
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: 768px) {
  .courses__grid {
    align-self: start;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .courses__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.course {
  border: 1px solid gainsboro;
  background-color: #FFFFFF;
}
.course__meta {
  background-color: #d92a2a;
  display: flex;
  justify-content: space-between;
  padding: 0 1rem;
  color: #FFFFFF;
  align-items: center;
}
.course__price {
  background-color: #999f44;
  padding: 0.5rem;
  font-size: 1.2rem;
  font-weight: 700;
  color: #000000;
  border-radius: 1rem;
}
.course__img {
  height: 30rem;
  width: 100%;
}
.course__content {
  padding: 2rem;
}
.course__info {
  color: #d92a2a;
}
.course__link {
  background-color: #d92a2a;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: 0.3s ease;
  border: none;
  /*      @include tablet {
          width: auto;
          display: inline-block;
      }  */
  display: block;
  font-size: 1.6rem;
  padding: 1rem;
}
.course__link:hover {
  cursor: pointer;
  opacity: 0.5;
  color: gray;
}

.school {
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/chef-bg.jpg");
  padding: 20rem 0;
  text-align: center;
  color: #FFFFFF;
}
.webp .school {
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/chef-bg.webp");
}
.avif .school {
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/chef-bg.avif");
}
@media screen and (-webkit-min-device-pixel-ratio: 2), screen and (min-resolution: 192dpi), screen and (min-resolution: 2dppx) {
  .school {
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/chef-bg.jpg");
  }
  .webp .school {
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/chef-bg.webp");
  }
  .avif .school {
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/chef-bg.avif");
  }
}
.school__title {
  font-weight: 700;
}
.school__text {
  font-size: 2.6rem;
  max-width: 50rem;
  margin: 0 auto;
}
.school__link {
  margin-top: 1rem;
  background-color: #d92a2a;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: 0.3s ease;
  border: none;
  /*      @include tablet {
          width: auto;
          display: inline-block;
      }  */
}
.school__link:hover {
  cursor: pointer;
  opacity: 0.5;
  color: gray;
}

.footer {
  padding: 3rem 0;
  border-bottom: 0.5rem solid #d92a2a;
}
@media (min-width: 768px) {
  .footer__grid {
    display: flex;
    justify-content: space-between;
  }
}
.footer__copyright {
  text-align: center;
  font-size: 2.4rem;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .footer__copyright {
    margin: 0;
  }
}