@use 'base/mixins' as m;
@use 'base/variables' as v;

.courses{
    background-color: v.$gray;
    padding: 5rem 0;
    &__title{
        @include m.title
    }
    &__grid{
        @include m.tablet {
            align-self: start;
            @include m.grid(2, 2rem ) ;
        }
        @include m.desktop {
            @include m.grid(3, 2rem ) ;
        }
    }
}

.course{
    border: 1px solid darken(v.$gray, 10%);
    background-color: v.$white;
    &__meta{
        background-color: v.$primary;
        display: flex;
        justify-content: space-between;
        padding: 0 1rem;
        color: v.$white;
        align-items: center;
    }
    &__price{
        background-color: v.$secondary;
        padding: .5rem;
        font-size: 1.2rem;
        font-weight: 700;
        color: v.$black;
        border-radius: 1rem;
    }

    &__img{
        height: 30rem;
        width: 100%;
    }
    &__content{
        padding: 2rem;
    }
    &__info{
        color: v.$primary;
    }
    &__link{
        @include m.btn(v.$primary, v.$white ) ;
        display: block;
        font-size: 1.6rem;
        padding: 1rem;
    }
}