@use 'base/mixins' as m;
@use 'base/variables' as v;

.us{
    padding: 5rem 0;

    &__title{
        @include m.title
    }
    &__grid{
        @include m.tablet {
            @include m.grid(3,2rem ) ;
        }
    }
    &__block{
        text-align: center;
    }
    &__img{
        margin: 0 auto;
    }
}